const INITIAL_STATE = {
  exercice: "SQUATS",
  interval: 2,
  currentInterval: 1,
  active: 10,
  currentActive: 10,
  passive: 5,
  currentPassive: 5,
  rest: 60,
  currentRest: 60,
  lap: 4,
  currentLap: 1,
  index: 1,
  workActive: true,
  restActive: false,
  pauseActive: false,
};

function counterReducer(state = INITIAL_STATE, action) {
  //////////////////////// active timer
  switch (action.type) {
    case "ACTIVE-TIMER":
      ///////////////// update lap
      if (state.currentInterval <= state.interval) {
        ///////////////// update interval
        if (state.currentActive > 0 && state.workActive === true) {
          return {
            ...state,
            currentActive: state.currentActive - 1,
          };
        } else if (state.currentActive <= 0) {
          return {
            ...state,
            workActive: false,
            restActive: true,

            currentActive: state.active,
          };
        } else if (state.restActive === true) {
          if (state.currentPassive > 0) {
            return {
              ...state,

              currentPassive: state.currentPassive - 1,
              /// jusqu'ici tout va bien
            };
          }

          if (state.currentPassive <= 0) {
            return {
              ...state,
              currentInterval: state.currentInterval + 1,
              // index: state.index + 1, //pas sÃ»r

              workActive: true,
              restActive: false,

              currentPassive: state.passive,
            };

            // if (state.currentlap === state.lap) {
            //   return {
            //     ...state,

            //     workActive: true,
            //     restActive: false,

            //     currentPassive: state.passive,
            //   };
            // }
          }
        }
      } else {
        return {
          ...state,
          currentInterval: 1,
          currentLap: state.currentLap + 1,
        };
      }
    ///////////////////////// reset

    case "RESET":
      return {
        ...state,
        currentActive: state.active,
        currentPassive: state.passive,
      };

    //////////////////////   interval
    case "MORE-INT":
      if (state.interval < 20) {
        return {
          ...state,
          interval: state.interval + 1,
        };
      } else {
        return {
          ...state,
          interval: state.interval,
        };
      }
    case "LESS-INT":
      if (state.interval > 1) {
        return {
          ...state,
          interval: state.interval - 1,
        };
      }
      return {
        ...state,
        interval: state.interval,
      };

    ///////////////////////   active

    case "MORE-ACT":
      if (state.active < 60) {
        return {
          ...state,
          active: state.active + 5,
          currentActive: state.currentActive + 5,
        };
      } else {
        return {
          ...state,
          active: state.active,
          currentActive: state.currentActive,
        };
      }
    case "LESS-ACT":
      if (state.active > 5) {
        return {
          ...state,
          active: state.active - 5,
          currentActive: state.currentActive - 5,
        };
      } else {
        return {
          ...state,
          active: state.active,
          currentActive: state.currentActive,
        };
      }

    /////////////////////// passive
    case "MORE-PAS":
      if (state.passive < 60) {
        return {
          ...state,
          passive: state.passive + 5,
          currentPassive: state.currentPassive + 5,
        };
      } else {
        return {
          ...state,
          passive: state.passive,
        };
      }
    case "LESS-PAS":
      if (state.passive > 4) {
        return {
          ...state,
          passive: state.passive - 5,
          currentPassive: state.currentPassive - 5,
        };
      } else {
        return {
          ...state,
          passive: state.passive,
        };
      }

    /////////////////////// rest
    case "MORE-RES":
      if (state.rest < 300) {
        return {
          ...state,
          rest: state.rest + 10,
        };
      } else {
        return {
          ...state,
          rest: state.rest,
        };
      }
    case "LESS-RES":
      if (state.rest > 9) {
        return {
          ...state,
          rest: state.rest - 10,
        };
      } else {
        return {
          ...state,
          rest: state.rest,
        };
      }

    /////////////////////// lap
    case "MORE-LAP":
      if (state.lap < 20) {
        return {
          ...state,
          lap: state.lap + 1,
        };
      } else {
        return {
          ...state,
          lap: state.lap,
        };
      }
    case "LESS-LAP":
      if (state.lap > 1) {
        return {
          ...state,
          lap: state.lap - 1,
        };
      } else {
        return {
          ...state,
          lap: state.lap,
        };
      }
  }

  return state;
}

export default counterReducer;
